<template>
	<div>
		<h3>Companies</h3>
		<b-alert variant="success" show>
			Create and modify companies
		</b-alert>
		<b-row>
			<b-col md="2">
				<b-form-input
					type="text"
					placeholder="Company Name"
					v-model="searchForm.name"/>
			</b-col>
			<b-col md="2">
				<b-button
					variant="primary"
					@click="fetch">
					Search
				</b-button>
			</b-col>
			<b-col md="8" class="text-right">
				<b-button variant="success" @click="add">
					<b-icon icon="plus" scale="1.5"></b-icon> Add
				</b-button>
			</b-col>
		</b-row>
		<b-table
			:fields='fields'
			:items='list'
			:busy="loading"
			outlined
			class="mt-3"
			responsive
		>
			<template #cell(index)="data">
				<p>{{ (searchForm.page - 1) * searchForm.perPage + data.index + 1 }}</p>
			</template>
			<template #cell(name)="data">
				<p>{{ data.value }}</p>
			</template>
			<template #cell(edit)="data">
				<b-button variant="success" size="sm" @click="edit(data.index)" class="mr-2">
					<b-icon icon="pencil"></b-icon> Edit
				</b-button>
				<b-button variant="danger" size="sm" @click="deleteById(data.index)">
					<b-icon icon="trash"></b-icon> Delete
				</b-button>
			</template>
		</b-table>
		<b-pagination
			v-model="searchForm.page"
			:total-rows="total"
			:per-page="searchForm.perPage"
			align="right">
		</b-pagination>

		<b-modal 
			v-model="addModalShow"
			title="Add Company"
			hide-header-close 
			@ok="doAdd"
			@cancel="addModalShow=false"
		>
			<b-row>
				<b-col md="4">Company Name</b-col>
				<b-col md="8">
					<b-form-input
						type="text"
						v-model="company.name"
						:state="!$v.company.name.required ? false : null"/>
					<b-form-invalid-feedback v-if="!$v.company.name.required">
						Name is required.
					</b-form-invalid-feedback>
				</b-col>
			</b-row>
		</b-modal>

		<b-modal 
			v-model="editModalShow"
			title="Edit Company"
			hide-header-close 
			@ok="doEdit"
		>
			<b-row>
				<b-col md="4">Company Name</b-col>
				<b-col md="8">
					<b-form-input
						type="text"
						v-model="company.name"
						:state="!$v.company.name.required ? false : null"/>
					<b-form-invalid-feedback v-if="!$v.company.name.required">
						Name is required.
					</b-form-invalid-feedback>
				</b-col>
			</b-row>
		</b-modal>

		<b-modal 
			v-model="deleteModalShow"
			title="Delete Company"
			hide-header-close 
			@ok="doDelete"
		>
			Are you sure you want to delete?
		</b-modal>
	</div>
</template>
<script>
	import { mapState } from 'vuex'
	import { required } from 'vuelidate/lib/validators'
	export default {
		name: 'CompanyView',
		mounted() {
			this.fetch()
		},
		data() {
			return {
				fields: [
					{ key: 'index', label: 'No' },
					{ key: 'name', label: 'Company Name' },
					{ key: 'edit', label: '-'}
				],
				loading: false,
				searchForm: {
					page: 1,
					perPage: 10,
					name: ''
				},
				addModalShow: false,
				editModalShow: false,
				deleteModalShow: false,
				company: {
					_id: '',
					name: ''
				}
			}
		},
		computed: {
			...mapState({
				list: state => state.company.list,
				total: state => state.company.total
			}) 
		},
		validations: {
			company: {
				name: { required }
			}
		},
		methods: {
			fetch() {
				this.loading = true
				this.$store.dispatch('company/fetch', this.searchForm).then(() => {
					this.loading = false
				})
			},
			add() {
				this.company = {
					_id: '',
					name: ''
				}
				this.addModalShow = true
			},
			doAdd(evt) {
				evt.preventDefault()

				this.$v.company.$touch()
				if (this.$v.company.$error) return

				this.addModalShow = false
				this.$store.dispatch('company/add', this.company).then(() => {
					this.$toasted.success('Add Success')
					this.fetch()
				})
			}, 
			edit(idx) {
				Object.keys(this.list[idx]).forEach(key => {
					this.company[key] = this.list[idx][key]
				})
				this.editModalShow = true
			},
			doEdit(evt) {
				evt.preventDefault()

				this.$v.company.$touch()
				if (this.$v.company.$error) return

				this.editModalShow = false
				this.$store.dispatch('company/edit', this.company).then(() => {
					this.$toasted.success('Edit Success')
					this.fetch()
				})
			},
			deleteById(idx) {
				this.company._id = this.list[idx]._id
				this.deleteModalShow = true
			},
			doDelete() {
				this.$store.dispatch('company/deleteById', this.company._id).then(() => {
					this.$toasted.success('Delete Success')
					this.fetch()
				})
			}
		},
		watch: {
			'searchForm.page': function() {
				this.fetch()
			}
		}
	}
</script>